.categories {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-evenly;
  height: 30vh;
  gap: 3%;
  margin-top: 1%;
  padding: 0 10%;
}
.category {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25%;
  background-color: #e7eff9;
  background-color: #4267b3;
  opacity: 0.9;
  color: #fff;
  font-size: 1.5rem;
  margin-bottom: 1%;
}
.category:hover {
  cursor: pointer;
  background-color: rgb(172, 131, 180) ;
  color:black;
}
.label {
  padding-top: 1%;
  padding-bottom: 1%;
  background-color: #8b9dc3;
  /* color: rgb(212, 211, 211); */
  color: white;
  width: 100%;
}
@media (max-width: 648px) {
  .categories {
    display: grid;
    height: 60vh;
  }
  .category {
    width: 100%;
    padding: 0.5em;
  }
}
